import { HostListener,Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Skills } from '../shared/skills';
import { SkillsService } from '../services/skills.service';
import { Experiences } from  '../shared/experiences';
import { ExperienceService } from '../services/experience.service';
import { Certifications } from '../shared/certifications';
import { CertificationsService } from '../services/certifications.service';
import { MatExpansionPanel } from '@angular/material/expansion';
import { NgbModal,ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { FormControl,Validators, FormBuilder, FormGroup } from '@angular/forms';
import { ContactService } from '../contact.service';




@Component({
  selector: 'app-skills',
  templateUrl: './skills.component.html',
  styleUrls: ['./skills.component.scss']
})
export class SkillsComponent implements OnInit {

  @ViewChild('panel1') firstPanel: MatExpansionPanel;
  @ViewChild('panel2') secondPanel: MatExpansionPanel;
  @ViewChild('panel3') thirdPanel: MatExpansionPanel;

  skills: Skills[];
  experiences: Experiences[];
  certifications: Certifications[];
  email = new FormControl('',[Validators.required,Validators.email]);
  closeResult='';
  FormData: FormGroup;

  constructor(private router: Router,
    private skillService: SkillsService, private experienceService: ExperienceService,
    private certificationService: CertificationsService, private route: ActivatedRoute,
    private modalService: NgbModal,private builder: FormBuilder, private contact: ContactService) { }

  ngOnInit(): void {
    const id = +this.route.snapshot.params['id'];
    this.skillService.getSkills().subscribe(skills => this.skills = skills);
    this.experienceService.getExperiences().subscribe(experiences => this.experiences = experiences);
    this.certificationService.getCertification().subscribe(certifications => this.certifications = certifications);

    this.FormData = this.builder.group({
      Fullname: new FormControl('', [Validators.required]),
      Email: new FormControl('', [Validators.compose([Validators.required, Validators.email])]),
      Comment: new FormControl('', [Validators.required])
      })
  }

  showInfo(){
    document.getElementById('info').style.opacity = '1';
  }

  collapseAccordions(digit){
    console.log(digit);
    if(digit==1){
      this.secondPanel.close();
      this.thirdPanel.close();
    }
    if(digit==2){
      this.firstPanel.close();
      this.thirdPanel.close();
    }
    if(digit==3){
      this.firstPanel.close();
      this.secondPanel.close();
    }
  }

  open(content) {
    this.modalService.open(content,{ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }

  getErrorMessage() {
    if(this.email.hasError('required')) {
      return 'You must enter an email';
    }
    return this.email.hasError('email') ? 'Not a valid email' : '';
  }

  /*onSubmit(FormData) {
    console.log(FormData)
    this.contact.PostMessage(FormData);
    this.modalService.dismissAll();
  }*/


}