import { Injectable } from '@angular/core';
import {SKILL} from '../shared/skill';
import {Skills} from '../shared/skills';
import { Observable, of } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SkillsService {

  constructor() { }

  getSkills(): Observable <Skills[]> {
    return of(SKILL)
  }

}
